import Page from 'components/Page';
import React, { useState, useEffect } from 'react';
import { getProducto } from '../service/apiProducto';
import { AgregarAlCarrito } from '../service/tienda';
// redux
import {useDispatch} from 'react-redux'
import { getProductosLocalStore } from '../redux/carrito'

// import {MdShoppingCart} from 'react-icons/md';
import { Button, Card, CardBody, CardImg, Col, Row } from 'reactstrap';

export default function Tienda() {
  const dispatch = useDispatch();
  const [productos, setproductos] = useState(null);
  useEffect(async () => {
    const rest = await getProducto();
    setproductos(rest);
  }, []);
  const AÃ±adirCompra = async(id,nombre,precio)=>{
    await AgregarAlCarrito(id,nombre,precio);
  }
  return (
    <Page
      title="Tienda"
      breadcrumbs={[{ name: 'cards', active: true }]}
      className="Cards"
    >
      <Row>
        { productos ? productos.map((item, index) => (          
          <>
            <Col md={3} sm={6} xs={12} className="mb-3">
              <Card
                inverse
                className="border-0 bg-gradient-theme"
                style={{ height: 'auto' }}
              >
                <CardImg
                  top
                  src={`${item.imagen}`}
                  style={{ width: 'auto', height: 250, padding: 2 }}
                  className="fluid"
                />
                <CardBody
                  className="align-items-center"
                  style={{ height: 'auto', color: 'black' }}
                >
                  <h6>
                    <b>Nombre: </b> <span>{item.nombre}</span>
                  </h6>
                  <h6>
                    <b>Precio: </b> <span>{item.precio}</span>
                  </h6>
                  <h6>
                    <b>Descuento: </b> <span>{item.descriento}%</span>
                  </h6>
                  <div className="d-flex justify-content-end">
                    <Button outline color="light" onClick={() => AÃ±adirCompra(item._id, item.nombre, item.precio) +  dispatch(getProductosLocalStore()) }>
                      AÃ±adir
                    </Button>
                  </div>
                </CardBody>
              </Card>
            </Col>
          </>
        )) : ''}
      </Row>
    </Page>
  );
}